body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, 'Helvetica Neue', Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.app {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  min-height: 100vh;
}

.loading-container {
  display: flex;
  align-items: center;
  justify-content: center;
  min-height: 100vh;
}

.loading-image {
  width: 150px;
  height: auto;
}

.content {
  width: 100%;
}

.header {
  display: flex;
  align-items: center;
  padding: 20px;
  background-color: #282c34;
  color: white;
}

.logo {
  width: 50px;
  height: auto;
  margin-right: 20px;
  transition: all 0.5s ease;
}

.header-title {
  font-size: 1.5em;
}

.main-content {
  padding: 20px;
}
